import Vue from 'vue';
import VueRouter from 'vue-router';

import Main from '@/views/main';
import Index from '@/views/index';
import Login from '@/views/login';
import ThemeList from '@/views/blog/themeList.vue'
import TagList from '@/views/blog/tagList.vue'
import BlogList from '@/views/blog/list.vue'
import BlogInfo from '@/views/blog/info.vue'
import BlogSave from '@/views/blog/save.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'login',
        component: Login,
        meta: ['登陆']
    },
    {
        path: '/',
        name: 'main',
        component: Main,
        meta: [],
        children:[
            {
                path: '/',
                name: 'index',
                component: Index,
                meta: ['首页']
            },
            {
                path: '/blog/theme',
                name: 'themeList',
                component: ThemeList,
                meta: ['主题列表']
            },
            {
                path: '/blog/tag',
                name: 'tagList',
                component: TagList,
                meta: ['标签列表']
            },
            {
                path: '/blog/list',
                name: 'blogList',
                component: BlogList,
                meta: ['博客列表']
            },
            {
                path: '/blog/info',
                name: 'blogInfo',
                component: BlogInfo,
                meta: ['博客详情']
            },
            {
                path: '/blog/save',
                name: 'blogSave',
                component: BlogSave,
                meta: ['博客保存']
            },
        ]
    },
];
const router = new VueRouter({
    mode: 'hash',
    routes
});


//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default router;