<template>
    <div class="login-page" :style="loginPageStyle">

        <div class="login-form">
            <el-form
                label-position='left'
                label-width='80px'
                :model="loginFormData"
                :rules="rules"
                ref='loginFormDataRef'>
                <el-form-item label='用户名' prop="username" class="login-form-item">
                    <el-input 
                        autofocus
                        v-model="loginFormData.username"
                        placeholder='输入用户名'>
                    </el-input>
                </el-form-item>
                <el-form-item label='密码' prop="password" class="login-form-item">
                    <el-input 
                        v-model="loginFormData.password"
                        show-password
                        placeholder='输入密码'>
                    </el-input>
                </el-form-item>
                <el-form-item label='验证码' prop="picCode" class="login-form-item">
                    <el-input 
                        v-model="loginFormData.picCode"
                        @keypress.native.enter="submitForm"
                        placeholder='输入验证码'>
                    </el-input>
                    <img :src="base64Captcha" alt="验证码" class="vertify_img" @click="getCaptchaData">
                </el-form-item>
                <el-form-item class="login-button">
                    <el-button type='primary' @click="submitForm" :loading="loginBtnState">
                        登录
                    </el-button>
                    <el-button @click="resetForm">
                        重置
                    </el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    computed: {
        ...mapGetters([
            'token','realname','authList','sysManager','permissionArray'
        ])
    },
    data(){
        return {
            loginPageStyle: {
                
            },
            loginBtnState: false,
            base64Captcha: '',
            loginFormData: {
                username: '',
                password: '',
                uuid: '',
                picCode: '',
            },
            rules: {
                username: [
                    { required: true, message: '请输入登录名', trigger: 'blur' },
                    { min: 4, max: 20, message: '长度在 4 到 20 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 100, message: '长度在 6 到 100 个字符', trigger: 'blur' }
                ],
                picCode: [
                    { required: true, message: '请输入验证码', trigger: 'blur' },
                ],
                
            }
        }
    },
    mounted:function () {   //自动触发写入的函数
        this.getCaptchaData();
        // this.getImage();
    },
    methods: {
        getImage(){
            this.$GaApi.getLoginPageBackgroundImage().then(res=>{
                console.log(res);
                this.loginPageStyle.backgroundImage = 'url('+res.data+')'
            })
        },
        submitForm(){
            this.loginBtnState = true;
            this.$refs.loginFormDataRef.validate((valid) => {
                if(valid){
                    this.$GaApi.login(this.loginFormData).then(res => {

                        this.$store.commit('CHANGE_TOKEN', res.data.token);
                        // this.getUserInfo().then(() => {
                        //     this.$router.push({name:'index'});
                        // })
                        this.$router.push({name:'index'});
                        this.loginBtnState = false;
                    }).catch(() => {
                        this.getCaptchaData();
                        this.loginBtnState = false;
                    })
                }
            })
        },
        resetForm(){
            this.$refs.loginFormDataRef.resetFields();
            this.$store.commit('RESET_USER');
            this.getCaptchaData();
        },
        getCaptchaData(){
            this.loginFormData.picCode = '';
            this.$GaApi.getCaptcha().then(res => {
                this.base64Captcha = res.data
                this.loginFormData.uuid = res.extraData
            })
        },
        getUserInfo(){
            return new Promise((resolve, reject) => {
                this.$GaApi.logUserInfo().then(res => {
                    let jjManagerUser = res.data.jjManagerUser;
                    let permissionList = res.data.jjManagerPermissionList;
                    let authList = this.$gaPermission.getAuthList(permissionList);

                    let param = {
                        realname: jjManagerUser.realname,
                        sysManager: jjManagerUser.sysManager,
                        permissionArray: permissionList,
                        authList: authList
                    }

                    this.$store.commit('SET_USER', param);
                    resolve()
                }).catch(()=> {
                    reject()
                })
            });
        },
    }
}
</script>

<style lang="stylus" scoped>
.login-page
    display: flex;
    justify-content: center;
    align-items: center;
    height 100%
    background-image url('https://cn.bing.com//th?id=OHR.LakePinatubo_ZH-CN5947011761_1920x1080.jpg&rf=LaDigue_1920x1080.jpg&pid=hp')
    

.login-form
    width 300px
    background-color #CCC
    background-color rgba(100, 100, 100, 0.8);
    padding 10px 10px 0 10px
    border-radius 10px
    box-shadow 0 0 9px 3px #999; 
    
.vertify_img
    position absolute
    right 1px
    bottom 2px
    border-radius 5px
    height 37px

</style>
