<template>
    <div class="list-page">
        <div class="list-page-search">
            <el-input
                placeholder="请输入标题"
                prefix-icon="el-icon-search"
                v-model="searchModel.title">
            </el-input>


            <el-select class="search-input" v-model="searchModel.themeId" placeholder="选择主题" @change="onThemeChange" clearable>
                <el-option
                    v-for="item in themeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-select class="search-input" v-model="searchModel.tagId" placeholder="选择标签" clearable>
                <el-option
                    v-for="item in tagList"
                    :key="item.id"
                    :label="item.tagName"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-date-picker
                  class="search-date-range"
                  
                  v-model="searchModel.pubDate"
                  type="daterange"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
            <el-button 
                type="info" 
                @click="getTagList" 
                plain>
                查询
            </el-button>
            <el-button 
                type="warning" 
                @click="clearSearch" 
                plain>
                重置
            </el-button>
        </div>

        <div class="list-page-add-btn">
            <el-button 
            @click="addBlog" 
            type="primary" 
            plain>
                新增
            </el-button>
        </div>

        <el-table
            :data="blogList"
            style="width: 100%"
            element-loading-text="拼命加载中" 
            border stripe
            fit
            highlight-current-row>
            <el-table-column
            fixed
            min-width="300"
            label="标题">
                <template slot-scope="scope">
                    <el-button @click="getInfo(scope.row)" type="text">
                        {{!scope.row.title ? '无标题' : scope.row.title}}
                    </el-button>
                </template>
            </el-table-column>
            <el-table-column
            prop="author"
            label="作者" width="80">
            </el-table-column>
            <el-table-column
            prop="pubTime"
            label="日期" width="100">
            </el-table-column>
            <el-table-column
                width="200"
                label="标签">
                <template slot-scope="scope">
                    {{scope.row.themeName}} {{scope.row.themeName && scope.row.tagName ? "; " : ""}} {{scope.row.tagName}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="管理" width="120" fixed="right">
                <template slot-scope="scope">
                    <el-button @click="editBlog(scope.row.id)" type="text" size="small">编辑</el-button>
                    <el-button @click="deleteBlog(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
            v-if="pageModel.total != 0"
          class="list-page-pagination"
          background
          layout="prev, pager, next"
          :current-page.sync="pageModel.page"
          :total="pageModel.total"
          @current-change="pageOnChange"
          >
        </el-pagination>
    </div>
</template>

<script>
export default {
    created(){
    },
    mounted(){
        this.onLoad();
        this.getList();
        this.getTagList();
        this.getThemeList();
    },
    computed:{
        searchModelCom(){
            return {searchModel:JSON.stringify(this.searchModel), pageModel: JSON.stringify(this.pageModel)};
        }
    },
    watch: {
        // '$route' () {
        //     // this.getData(this.$route.query.id)
        //     console.log('参数改变了')
        //     console.log(this.$route.query)
        // },
        // pageModel:{
        //     handler(newVal,oldVal){
        //         if(newVal.page != oldVal.page){
        //             this.listeningDataChange(newVal);
        //         }
        //     },
        //     deep:true
        // },
        searchModelCom:{
            handler(newVal,oldVal){
                let flag = this.$gaJson.Compare(JSON.parse(newVal.searchModel), JSON.parse(oldVal.searchModel));
                
                if(!flag || JSON.parse(newVal.pageModel).page != JSON.parse(oldVal.pageModel).page){
                    this.listeningDataChange(newVal);
                }
            },
            deep:true
        },
    },
    methods: {
        listeningDataChange (val){
          console.log(val)
          const query=JSON.parse(JSON.stringify(this.$route.query))

            let searchModelTemp = JSON.parse(val.searchModel);
          for(var key in searchModelTemp){
              if(Object.keys(this.searchModel).indexOf(key) == -1 && Object.keys(this.pageModel).indexOf(key) == -1){
                  continue;
              }
            if(searchModelTemp[key]){
              query[key] = searchModelTemp[key]
            }else{
              delete query[key]
            }
          }
            let pageModelTemp = JSON.parse(val.pageModel);
          for(var key2 in pageModelTemp){
              if(Object.keys(this.searchModel).indexOf(key2) == -1 && Object.keys(this.pageModel).indexOf(key2) == -1){
                  continue;
              }
            if(pageModelTemp[key2]){
              query[key2] = pageModelTemp[key2]
            }else{
              delete query[key2]
            }
          }
        //   if(this.$route.query['currentPage'] == this.currentPage){
        //     query['currentPage'] = 1;
        //   }
            delete query['total']
          this.$router.push({ path: this.$route.path, query })
          this.getList();
        },
        getInfo(row){
            console.log(row.id);
            this.$router.push({name:'blogInfo',query:{id: row.id}});
        },
        editBlog(id){
            this.$router.push({name:'blogSave',query:{id: id}});
        },
        deleteBlog(row){
            let title = row.title;
            if(title == ""){
                title = "无标题";
            }
            this.$confirm(`是否确认删除《${title}》?`, '提示', {
                confirmButtonText: '删除',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$GaApi.deleteBlogApi({id: row.id}).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.getList();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });          
            });
            
        },
        onLoad(){
            let searchModelTemp = this.$gaJson.deepClone(this.searchModel);
            // let pageModelTemp = this.$gaJson.deepClone(this.pageModel);

            let queryData = this.$route.query;
            let month = queryData.month;
            if(month){
                let startDate = month + '-01';
                let endDate = month + '-' + new Date((month.split('-')[0], month.split('-')[1]), 0).getDate();
                searchModelTemp.pubDate = [startDate, endDate];
            }
            let tagId = queryData.tagId;
            if(tagId){
                searchModelTemp.tagId = tagId;
            }
            let themeId = queryData.themeId;
            if(themeId){
                searchModelTemp.themeId = themeId;
            }
            let pubDate = queryData.pubDate;
            if(pubDate){
                searchModelTemp.pubDate = pubDate;
            }
            let title = queryData.title;
            if(title){
                searchModelTemp.title = title;
            }
            let page = queryData.page;
            if(page){
                this.pageModel.page = parseInt(page);
            }
            this.searchModel = searchModelTemp;

        },
        getList(){
            let _this = this;
            let data = {};
            let queryData = this.$route.query;
            console.log(queryData);
            if(queryData && JSON.stringify(queryData) != "{}"){
                data = {
                    page: queryData.page - 1,
                    title: queryData.title, 
                    tagId: queryData.tagId, 
                    themeId: queryData.themeId, 
                    authorId: queryData.authorId,
                };
                if(queryData.pubDate){
                    data.startDate = queryData.pubDate[0]
                    data.endDate = queryData.pubDate[1]
                }
            }
            
            this.$GaApi.getBlogListApi(data).then(data => {
                _this.blogList = data.data
                _this.pageModel.total = parseInt(data.totalElements)
            }).then(() => {
                // let queryData = this.$route.query;
                // let page = queryData.page;
                // if(page){
                //     _this.pageModel.page = parseInt(page);
                //     _this.pageOnChange(parseInt(page))
                // }
            })
        },
        getThemeList(){
            this.$GaApi.getThemeListApi({page: -1}).then(res=>{
                this.themeList = res.data
            })
        },
        onThemeChange(){
            this.searchModel.tagId = null;
            this.getTagList();
        },
        /**获取标签列表 */
        getTagList(){
            let data = {page: -1};
            let themeList = [];
            let _themeId = this.searchModel.themeId;
            if(_themeId){
                themeList.push(_themeId);
                data.themeId = themeList;
            }
            this.$GaApi.getTagListApi(data).then(res => {
                this.tagList = res.data
            })
        },
        addBlog(){
            this.$GaApi.insertBlogApi().then(res=>{
                this.$router.push({name:'blogSave',query:{id: res.data}});
            })
        },
        clearSearch(){
            this.searchModel.title = '';
            this.searchModel.tagId = null;
            this.searchModel.themeId = null;
            this.searchModel.pubDate = [];
            this.pageModel.page = 1;
        },
        pageOnChange(val){
            this.pageModel.page = val;
        },
    },
    data() {
        return {
            authorList: [],
            themeList: [],
            tagList: [],
            scopeList: [{
                value: '1',
                label: '标签1'
            }, {
                value: '2',
                label: '标签2'
            }],
            blogList: [],
            searchModel: {
                title: '',
                authorId: null,
                pubDate: [],
                themeId: null,
                tagId: null,
                scope: null,
            },
            pageModel: {
                total: 0,
                page: 1
            }
        }
    }
}
</script>