    <div>
        <ga-editor :originData="text" ref="editorRef"/>
        <el-button @click="getEditorData">get content</el-button>
    </div>
</template> -->

<template>
  <div class="editor-area">
        <div class="title">
            <el-input v-model="title" placeholder="请输入标题"></el-input>
        </div>
        <div class="author">
            <el-select class="search-input" v-model="themeId" @change="onThemeChange" placeholder="选择主题" clearable multiple>
                <el-option
                    v-for="item in themeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-select class="search-input" v-model="tagId" placeholder="选择标签" clearable multiple>
                <el-option
                    v-for="item in tagList"
                    :key="item.id"
                    :label="item.tagName"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-select class="search-input" v-model="scopeType" placeholder="可见设置">
                <el-option
                    v-for="item in scopeTypeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-switch
              v-model="passwordSet"
              active-color="#13ce66"
              active-text="加密"
              inactive-text="不加密">
            </el-switch>
            <el-date-picker
              v-model="pubTime"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
        </div>
        <div class="content">
            <!-- <quill-editor
                class="editor-plugin"
                ref="myQuillEditor"
                :content="content"
                :options="editorOption"
                @change="onEditorChange($event)"
            /> -->
            <ga-editor 
              :originData="originData" 
              ref="editorRef" 
              :height='500' 
              :uploadFileName="'file'"
              :uploadImgHeaders="uploadImgHeaders"
              :uploadImgServer="'https://anmeng.320.io/api/upload-service/wangEditorImage'"/>
        </div>
        <div class="button">
            <el-row class="button-row">
                <el-button type="danger" icon="el-icon-close" circle @click="cancelCommit"></el-button>
                <el-button type="info" plain @click="dialogFormVisible = true">历史版本</el-button>
                <el-button type="success" icon="el-icon-check" circle @click="commit(true)"></el-button>
            </el-row>
        </div>
        
        <!-- 对话框 -->
        <el-dialog 
          title="历史版本" 
          width="90%"
          :visible.sync="dialogFormVisible">
          <old-version-list
            style="height:100%"
            @select="getMsgFromSon"
            :blogId="id">

          </old-version-list>
        </el-dialog>

  </div>
</template>

<script>
  import oldVersionList from '@/components/blog/OldVersionBlog.vue';
  export default {
    components: {
      oldVersionList: oldVersionList
    },
    data () {
      return {
          timer: '',
          savedContent: null,
          dialogFormVisible: false,

            text: '<p>主类添加缓存支持注解</p><pre class="ql-syntax" spellcheck="false">@EnableCaching </pre><p><br></p><p>配置文件中添加开启二级缓存注解</p><pre class="ql-syntax" spellcheck="false">mybatis-plus.configuration.cache-enabled=true </pre><p><br></p><p>对应mapper.xml中添加一行配置</p><pre class="ql-syntax" spellcheck="false">&lt;?xml version="1.0" encoding="UTF-8" ?&gt; &lt;!DOCTYPE mapper PUBLIC "-//mybatis.org//DTD Mapper 3.0//EN" "http://mybatis.org/dtd/mybatis-3-mapper.dtd"&gt; &lt;mapper namespace="com.zsy.mapper.CarouselMapper"&gt; &lt;cache-ref namespace="com.zsy.mapper.CarouselMapper"/&gt; 这一行 &lt;/mapper&gt; </pre><p><br></p><p>mapper.java中添加一个注解</p><pre class="ql-syntax" spellcheck="false">@CacheNamespace(implementation=MybatisRedisCache.class,eviction=MybatisRedisCache.class) </pre><p><br></p><p>该文件如下</p><pre class="ql-syntax" spellcheck="false">import cn.hutool.core.util.StrUtil; import cn.hutool.crypto.SecureUtil; import com.fasterxml.jackson.annotation.JsonAutoDetect; import com.fasterxml.jackson.annotation.JsonInclude; import com.fasterxml.jackson.annotation.PropertyAccessor; import com.fasterxml.jackson.databind.DeserializationFeature; import com.fasterxml.jackson.databind.ObjectMapper; import com.fasterxml.jackson.databind.SerializationFeature; import com.fasterxml.jackson.datatype.jsr310.JavaTimeModule; import lombok.extern.slf4j.Slf4j; import org.apache.ibatis.cache.Cache; import org.springframework.data.redis.connection.RedisServerCommands; import org.springframework.data.redis.core.Cursor; import org.springframework.data.redis.core.RedisCallback; import org.springframework.data.redis.core.RedisTemplate; import org.springframework.data.redis.core.ScanOptions; import org.springframework.data.redis.serializer.Jackson2JsonRedisSerializer; import org.springframework.data.redis.serializer.RedisSerializer; import org.springframework.data.redis.serializer.StringRedisSerializer; import org.springframework.util.CollectionUtils; import java.util.HashSet; import java.util.Set; import java.util.concurrent.locks.ReadWriteLock; import java.util.concurrent.locks.ReentrantReadWriteLock; /** * @class: MybatisRedisCache */ @Slf4j public class MybatisRedisCache implements Cache { // 读写锁 private final ReadWriteLock readWriteLock = new ReentrantReadWriteLock(true); //这里使用了redis缓存，使用springboot自动注入 private RedisTemplate&lt;String, Object&gt; redisTemplate; private String id; public MybatisRedisCache(final String id) { if (id == null) { throw new IllegalArgumentException("Cache instances require an ID"); } this.id = id; } private RedisTemplate&lt;String, Object&gt; getRedisTemplate(){ if (redisTemplate == null) { //由于启动期间注入失败，只能运行期间注入，这段代码可以删除 redisTemplate = (RedisTemplate&lt;String, Object&gt;) SpringUtil.getBean("redisTemplate"); //Use Jackson 2Json RedisSerializer to serialize and deserialize the value of redis (default JDK serialization) Jackson2JsonRedisSerializer jackson2JsonRedisSerializer = new Jackson2JsonRedisSerializer(Object.class); ObjectMapper objectMapper = new ObjectMapper(); objectMapper.setVisibility(PropertyAccessor.ALL, JsonAutoDetect.Visibility.ANY); //将类名称序列化到json串中 objectMapper.enableDefaultTyping(ObjectMapper.DefaultTyping.NON_FINAL); //设置输入时忽略JSON字符串中存在而Java对象实际没有的属性 objectMapper.configure(DeserializationFeature.FAIL_ON_UNKNOWN_PROPERTIES, false); // 解决jackson2无法反序列化LocalDateTime的问题 objectMapper.disable(SerializationFeature.WRITE_DATES_AS_TIMESTAMPS); objectMapper.registerModule(new JavaTimeModule()); jackson2JsonRedisSerializer.setObjectMapper(objectMapper); //Use String RedisSerializer to serialize and deserialize the key value of redis RedisSerializer redisSerializer = new StringRedisSerializer(); //key redisTemplate.setKeySerializer(redisSerializer); redisTemplate.setHashKeySerializer(redisSerializer); //value redisTemplate.setValueSerializer(jackson2JsonRedisSerializer); redisTemplate.setHashValueSerializer(jackson2JsonRedisSerializer); redisTemplate.afterPropertiesSet(); } return redisTemplate; } @Override public String getId() { return this.id; } private String getKey(Object key){ String idMd5 = SecureUtil.md5(this.id); String keyMd5 = SecureUtil.md5(StrUtil.toString(key)); return idMd5 + ":"+keyMd5; } @Override public void putObject(Object key, Object value) { if (value != null) { String keyMd5 = getKey(key); getRedisTemplate().opsForValue().set(keyMd5, value); } } @Override public Object getObject(Object key) { try { if (key != null) { String keyMd5 = getKey(key); return getRedisTemplate().opsForValue().get(keyMd5); } } catch (Exception e) { e.printStackTrace(); log.error("缓存出错 "); } return null; } @Override public Object removeObject(Object key) { if (key != null) { getRedisTemplate().delete(getKey(key)); } return null; } @Override public void clear() { log.debug("清空缓存"); try { String idMd5 = SecureUtil.md5(this.id); Set&lt;String&gt; keys = redisTemplate.keys("*" + idMd5 + ":*"); if (!CollectionUtils.isEmpty(keys)) { getRedisTemplate().delete(keys); } } catch (Exception e) { log.error("清空缓存", e); } } public Set&lt;String&gt; scan(String matchKey) { Set&lt;String&gt; keys = getRedisTemplate().execute((RedisCallback&lt;Set&lt;String&gt;&gt;) connection -&gt; { Set&lt;String&gt; keysTmp = new HashSet&lt;&gt;(); Cursor&lt;byte[]&gt; cursor = connection.scan(new ScanOptions.ScanOptionsBuilder().match("*" + matchKey + "*").count(1000).build()); while (cursor.hasNext()) { keysTmp.add(new String(cursor.next())); } return keysTmp; }); return keys; } @Override public int getSize() { Long size = getRedisTemplate().execute((RedisCallback&lt;Long&gt;) RedisServerCommands::dbSize); return size.intValue(); } @Override public ReadWriteLock getReadWriteLock() { return this.readWriteLock; } } </pre><p><br></p><p>还有一个文件</p><pre class="ql-syntax" spellcheck="false">import org.springframework.beans.BeansException; import org.springframework.context.ApplicationContext; import org.springframework.context.ApplicationContextAware; import org.springframework.stereotype.Component; @Component public class SpringUtil implements ApplicationContextAware { private static ApplicationContext applicationContext; @Override public void setApplicationContext(ApplicationContext applicationContext) throws BeansException { SpringUtil.applicationContext = applicationContext; } public static Object getBean(String name){ return applicationContext.getBean(name); } public static &lt;T&gt; T getBean(String name, Class&lt;T&gt; clazz){ return applicationContext.getBean(name, clazz); } public static &lt;T&gt; T getBean(Class&lt;T&gt; clazz){ return applicationContext.getBean(clazz); } } </pre>',

          id: null,
          title: null,
          originData: null,
          content: null,
          editorOption: {
            // Some Quill options...
          },

          themeList: [],
          tagList: [],
          themeId: [],
          tagId: [],
          pubTime: null,

          passwordSet: false,
          scopeType: "ALL",
          scopeTypeList:[{id: "ALL", name: "全部可见"},{id:"ONLYSELF", name: "只能自己见"}],
          uploadImgHeaders:{
            token: this.$store.getters.token
          }

      }
    },
    methods: {
      showOldVersionDialog(){
        if(this.content != this.savedContent){
          this.commit();
        }
        this.dialogFormVisible = true;
      },
      getMsgFromSon(msg){
        this.title = msg.title;
        this.content = msg.content;
        this.dialogFormVisible = false
      },
      onEditorBlur(quill) {
        console.log('editor blur!', quill)
      },
      onEditorFocus(quill) {
        console.log('editor focus!', quill)
      },
      onEditorReady(quill) {
        console.log('editor ready!', quill)
      },
      onEditorChange({ quill, html, text }) {
        console.log('editor change!', quill, html, text)
        this.content = html
      },
      getThemeList(){
        this.$GaApi.getThemeListApi({page: -1}).then(res=>{
          this.themeList = res.data
        })
      },
      onThemeChange(){
        console.log(this.themeId)
        this.tagId = null;
        this.getTagList();
      },
      /**获取标签列表 */
      getTagList(){
        let data = {page: -1};
        if(this.themeId && this.themeId.length > 0){
          data.themeId = this.themeId.join();
        }
        this.$GaApi.getTagListApi(data).then(res => {
          this.tagList = res.data;
        })
      },
      cancelCommit(){
        this.$confirm('确认不保存并返回到博客列表吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.go(-1);
        }).catch(() => {
                  
        });
      },
      commit(backToLastRoute){
        let data = {};
        if(this.id){
          data.id = this.id;
        }
        data.title = this.title;
        data.content = this.$refs.editorRef.editorData;
        if(this.themeId && this.themeId.length > 0){
          let themeDataArray = [];
          for(let themeItem of this.themeId){
            let themeDataItem = {"id":themeItem}
            themeDataArray.push(themeDataItem);
          }
          data.themeInfo = themeDataArray;
        }
        if(this.tagId && this.tagId.length > 0){
          let themeDataArray = [];
          for(let themeItem of this.tagId){
            let themeDataItem = {"id":themeItem}
            themeDataArray.push(themeDataItem);
          }
          data.tagInfo = themeDataArray;
        }
        data.passwordSet = this.passwordSet?1:0;
        data.scopeType = this.scopeType;
        data.pubTime = this.pubTime;
        this.$GaApi.saveBlogApi(data).then(res=>{
          console.log(res)
          this.savedContent = this.$refs.editorRef.editorData;
          // this.$router.push({name: "blogList"})
          if(backToLastRoute){
            this.$router.go(-1);
          }
        })
      },
      getInfo(){
        let blogId = this.$route.query.id;
        if(!blogId){
          return;
        }
        this.id = blogId;
        this.$GaApi.getBlogInfoApi({id: blogId}).then(res => {
          let blogInfo = res.data;
          this.title = blogInfo.title;
          this.content = blogInfo.content;
          this.originData = blogInfo.content;
          this.savedContent = blogInfo.content;
          this.pubTime = blogInfo.pubTime;
          this.passwordSet = blogInfo.passwordSet == 1?true:false;
          this.scopeType =  blogInfo.scopeType;
          if(blogInfo.themeInfo){
            for(let item of blogInfo.themeInfo){
              this.themeId.push(String(item.id));
            }
          }
          if(blogInfo.tagInfo){
            for(let item of blogInfo.tagInfo){
              this.tagId.push(String(item.id));
            }
          }
        })
      }
    },
    mounted() {
      this.getTagList();
      this.getThemeList();
      this.getInfo();

      this.timer = setInterval(()=>{
        this.content = this.$refs.editorRef.editorData;
        if(this.id && this.content != this.savedContent){
          this.commit(false);
          this.$message({
            message: '恭喜你，文章已自动保存',
            type: 'success',
            center: true
          });
        }
      }, 60*1000);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    }
  }
</script>

<style>
.editor-area{
    display: flex;
    flex-direction: column;
}

.content{
    margin-bottom: 1rem;
}
.button-row{
    display: flex;
    justify-content: center;
}
.el-dialog,.el-dialog__body{
  height: 70%;
}

</style>