<template>
    <div class="list-page">

        <div class="list-page-search">
            <el-input
                placeholder="请输入主题名称"
                prefix-icon="el-icon-search"
                v-model="searchModel.name">
            </el-input>
            <el-select class="search-input" v-model="searchModel.tagId" @change="getThemeList" placeholder="选择标签" clearable>
                <el-option
                    v-for="item in tagList"
                    :key="item.id"
                    :label="item.tagName"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-button 
                type="info" 
                @click="getThemeList" 
                plain>
                查询
            </el-button>
            <el-button 
                type="warning" 
                @click="clearSearch" 
                plain>
                重置
            </el-button>
        </div>

        <div class="list-page-add-btn">
            <el-button 
            @click="form.dialogFormVisible = true" 
            type="primary" 
            plain>
                新增
            </el-button>
        </div>

        <el-table
            :data="themeList"
            style="width: 100%"
            element-loading-text="拼命加载中" 
            border stripe
            fit
            highlight-current-row>
            <el-table-column align="center" label="序号" width="80">
                <template slot-scope="scope">
                    <span v-text="scope.$index + 1"> </span>
                </template>
            </el-table-column>

            <el-table-column
      prop="name"
      label="主题名称"
      width="150">
    </el-table-column>
    <el-table-column
      prop="tagName"
      label="附属标签">
    </el-table-column>
    <el-table-column
      label="创建时间"
      width="120">
      <template slot-scope="scope">
      {{!scope.row.createTime?"":scope.row.createTime.substring(0,10)}}
      </template>
    </el-table-column>

            <el-table-column align="center" label="管理" width="220">
                <template slot-scope="scope">
                    <el-button @click="showDialog(scope.row)" type="text" size="small">修改</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
          class="list-page-pagination"
          background
          layout="prev, pager, next"
          :current-page="pageModel.page"
          :total="pageModel.total"
          @current-change="pageOnChange"
          >
        </el-pagination>

        <el-dialog title="主题" 
            :visible.sync="form.dialogFormVisible"
            :before-close="handleClose"
            width="30%">
            <el-form 
                :model="form"
                label-position='left'
                label-width='80px'
                :rules="rules"
                ref='loginFormDataRef'>
                <el-form-item label="主题名称" prop="name">
                    <el-input v-model="form.name" placeholder='输入主题名称'></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="themeSave">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    mounted(){
        this.getThemeList();
        this.getTagList();
    },
    methods: {
        showDialog(row){
            this.form.dialogFormVisible = true;
            this.form.name = row.name;
            this.form.id = row.id;
        },
        handleClose(){
            this.form.dialogFormVisible = false;
            this.form.name = '';
            this.form.id = null;
        },
        clearSearch(){
            this.searchModel.name = '';
            this.searchModel.tagId = null;
            this.pageModel.page = 1;
            this.getThemeList();
        },
        pageOnChange(val){
            this.pageModel.page = val;
            this.getThemeList();
        },
        getThemeList(){
            let data = {
                page: this.pageModel.page - 1,
                tagId: this.searchModel.tagId, 
                name: this.searchModel.name
            };
            this.$GaApi.getThemeListApi(data).then(res=>{
                this.themeList = res.data;
                this.pageModel.total = parseInt(res.totalElements)
            })
        },
            /**获取标签列表 */
        getTagList(){
            let data = {page: -1};
            this.$GaApi.getTagListApi(data).then(res => {
                this.tagList = res.data
            })
        },
        themeSave(){
            this.$refs.loginFormDataRef.validate((valid) => {
                if(valid){
                    if(this.form.id){
                        this.$GaApi.updateThemeApi(this.form).then(()=>{
                            this.getThemeList();
                        })
                        this.handleClose();
                    }else{
                        this.$GaApi.insertThemeApi(this.form).then(()=>{
                            this.getThemeList();
                        })
                        this.handleClose();
                    }
                }
            })
        }
    },
    data() {
        return {
            createTime: null,
            themeList: [],
            tagList: [],
            form: {
                name: null,
                id: null,
                dialogFormVisible: false,
            },
            rules: {
                name: [
                    { required: true, message: '请输入主题名称', trigger: 'blur' },
                    { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
                ],
            },
            searchModel: {
                name: '',
                tagId: null
            },
            pageModel: {
                total: 0,
                page: 1
            }
        }
    }
}
</script>