<template>
    <div class="list-page">
        <div class="list-page-search">
            <el-input
                placeholder="请输入标签"
                prefix-icon="el-icon-search"
                v-model="searchModel.tagName">
            </el-input>
            <el-select class="search-input" v-model="searchModel.themeId" @change="getTagList" placeholder="选择标签" clearable>
                <el-option
                    v-for="item in themeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select>
            <el-button 
                type="info" 
                @click="getTagList" 
                plain>
                查询
            </el-button>
            <el-button 
                type="warning" 
                @click="clearSearch" 
                plain>
                重置
            </el-button>
        </div>

        <div class="list-page-add-btn">
            <el-button 
            @click="form.dialogFormVisible = true" 
            type="primary" 
            plain>
                新增
            </el-button>
        </div>

        <el-table
            :data="tagList"
            style="width: 100%"
            element-loading-text="拼命加载中" 
            border stripe
            fit
            highlight-current-row>
            <el-table-column align="center" label="序号" width="80">
                <template slot-scope="scope">
                    <span v-text="scope.$index + 1"> </span>
                </template>
            </el-table-column>

            <el-table-column
            prop="tagName"
            label="标签名称"
            width="150">
            </el-table-column>
            <el-table-column
            prop="themeName"
            label="所属主题">
            </el-table-column>
            <el-table-column
            label="创建时间"
            width="120">
            <template slot-scope="scope">
            {{!scope.row.createTime?"":scope.row.createTime.substring(0,10)}}
            </template>
            </el-table-column>

            <el-table-column align="center" label="管理" width="220">
                <template slot-scope="scope">
                    <el-button @click="showDialog(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click="showAssignDialog(scope.row)" type="text" size="small">分配</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
          class="list-page-pagination"
          background
          layout="prev, pager, next"
          :current-page="pageModel.page"
          :total="pageModel.total"
          @current-change="pageOnChange"
          >
        </el-pagination>

        <el-dialog title="标签" 
            :visible.sync="form.dialogFormVisible"
            :before-close="handleClose"
            width="30%">
            <el-form 
                :model="form"
                label-position='left'
                label-width='80px'
                :rules="rules"
                ref='loginFormDataRef'>
                <el-form-item label="标签名称" prop="name">
                    <el-input v-model="form.tagName" placeholder='输入主题名称'></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="tagSave">确 定</el-button>
            </div>
        </el-dialog>

              <!-- 弹窗 -->
      <el-dialog title="保存" :visible.sync="assignDialogForm.dialogFormVisible" @close="handleClose">
        <el-form :model="assignDialogForm">
          <el-form-item label="主题">
            <el-select v-model="assignDialogForm.themeInfo" placeholder="请选择主题" multiple>
              <el-option
                  v-for="item in themeList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="handleClose">取 消</el-button>
          <el-button type="primary" @click="assignTheme">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
export default {
    mounted(){
        this.getThemeList();
        this.getTagList();
    },
    methods: {
        showDialog(row){
            this.form.dialogFormVisible = true;
            this.form.tagName = row.tagName;
            this.form.id = row.id;
        },
        handleClose(){
            this.form.dialogFormVisible = false;
            this.form.tagName = '';
            this.form.id = null;
            this.assignDialogForm.id = null;
            this.assignDialogForm.themeInfo = null;
        },
        clearSearch(){
            this.searchModel.tagName = '';
            this.searchModel.themeId = null;
            this.pageModel.page = 1;
            this.getTagList();
        },
        pageOnChange(val){
            this.pageModel.page = val;
            this.getTagList();
        },
        getThemeList(){
            let data = {page: -1};
            this.$GaApi.getThemeListApi(data).then(res=>{
                this.themeList = res.data;
            })
        },
            /**获取标签列表 */
        getTagList(){
            let data = {
                page: this.pageModel.page - 1,
                themeId: this.searchModel.themeId, 
                tagName: this.searchModel.tagName
            };
            this.$GaApi.getTagListApi(data).then(res => {
                this.tagList = res.data;
                this.pageModel.total = parseInt(res.totalElements)
            })
        },
        tagSave(){
            this.$refs.loginFormDataRef.validate((valid) => {
                if(valid){
                    if(this.form.id){
                        this.$GaApi.updateTagApi(this.form).then(()=>{
                            this.getTagList();
                        })
                        this.handleClose();
                    }else{
                        this.$GaApi.insertTagApi(this.form).then(()=>{
                            this.getTagList();
                        })
                        this.handleClose();
                    }
                }
            })
        },
        showAssignDialog(row){
            if(!row){
                return;
            }
            this.assignDialogForm.dialogFormVisible = true;
            // this.assignDialogForm.themeInfo = row.tagName;
            this.assignDialogForm.id = row.id;
            let data = {
                id: row.id
            }
            let _this = this;
            this.$GaApi.getTagInfoApi(data).then(res => {
                let themeIdList = [];
                let _themeInfo = res.data.themeInfo;
                if(_themeInfo){
                    for(let _themeId of _themeInfo){
                        themeIdList.push(String(_themeId['id']));
                    }
                }
                _this.assignDialogForm.themeInfo = themeIdList;
            }).then(()=>{
                console.log(JSON.stringify(_this.assignDialogForm.themeInfo));
            })
        },
        assignTheme(){
            let _this = this;
            if(!this.assignDialogForm.id){
                return;
            }
            
            let data = {
                tagId: this.assignDialogForm.id,
                themeIdList: this.assignDialogForm.themeInfo
            };
            if(this.assignDialogForm.themeInfo != null){
                data.themeIdList = this.assignDialogForm.themeInfo.join();
            }
            
            this.$GaApi.assignThemeToTagApi(data).then(()=>{
                _this.getTagList();
            });
            this.assignDialogForm.dialogFormVisible = false;
        }
    },
    data() {
        return {
            createTime: null,
            themeList: [],
            tagList: [],
            form: {
                tagName: null,
                id: null,
                dialogFormVisible: false,
            },
            assignDialogForm: {
                dialogFormVisible: false,
                id: null,
                themeInfo: [],
            },
            rules: {
                tagName: [
                    { required: true, message: '请输入主题名称', trigger: 'blur' },
                    { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
                ],
            },
            searchModel: {
                tagName: '',
                themeId: null
            },
            pageModel: {
                total: 0,
                page: 1
            }
        }
    }
}
</script>