import GaTool from 'gatool'

var GaApi = {
    getLoginPageBackgroundImage(){
        return GaTool.HttpRequest.getRequest({
            url: 'https://anmeng.320.io/api/userinfo-service/login/background/image',
            method: "POST"
        });
    },
    getCaptcha(){
        return GaTool.HttpRequest.getRequest({
            url: '/userinfo-service/login/captcha',
            method: "POST"
        });
    },
    login(data){
        return GaTool.HttpRequest.getRequest({
            url: '/userinfo-service/login/login',
            method: "POST",
            data
        });
    },
    getThemeListApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-theme-info/theme/list',
            method: "POST",
            data
        });
    },
    getTagListApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/list',
            method: "POST",
            data
        });
    },
    getTagInfoApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/get',
            method: "POST",
            data
        });
    },
    getAuthorListApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/list',
            method: "POST",
            data
        });
    },
    getBlogInfoApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-info/info/get',
            method: "POST",
            data
        });
    },
    getBlogListApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-info/info/list',
            method: "POST",
            data
        });
    },
    insertBlogApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-info/info/insert',
            method: "POST",
            data
        });
    },
    deleteBlogApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-info/info/delete',
            method: "POST",
            data
        });
    },
    saveBlogApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-info/info/save',
            method: "POST",
            data,
            headers: {
                "Content-Type": "application/json;charset=UTF-8"
            }
        });
    },
    updateTagApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/update',
            method: "POST",
            data
        });
    },
    insertTagApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/insert',
            method: "POST",
            data
        });
    },
    assignThemeToTagApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-tag-info/tag/assignTheme',
            method: "POST",
            data
        });
    },
    updateThemeApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-theme-info/theme/update',
            method: "POST",
            data
        });
    },
    insertThemeApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-theme-info/theme/insert',
            method: "POST",
            data
        });
    },
    blogMonthApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/statistics/blog/month',
            method: "POST",
            data
        });
    },
    blogTagApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/statistics/blog/tagCount',
            method: "POST",
            data
        });
    },
    blogThemeApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/statistics/blog/themeCount',
            method: "POST",
            data
        });
    },
    oldVersionBlogListApi(data){
        return GaTool.HttpRequest.getRequest({
            url: '/blog-service/blog-old-version/blog/list',
            method: "POST",
            data
        });
    },
};
export default GaApi;