import Vue from 'vue'
import App from './App.vue'
// elementUI 前端框架
import ElementUI from 'element-ui';
// elementUI 框架样式 
import 'element-ui/lib/theme-chalk/index.css';
import 'gatool/src/assets/css/listPage.css';
import 'gatool/src/assets/css/gaCss.css';
import 'highlight.js/styles/monokai-sublime.css'

// gatool 开发插件
import GaTool from 'gatool';
import router from './config/router';
import '@/config/routerInterceptor';
import GaApi from './config/api';
import store from '@/store/index';

Vue.prototype.$GaRouter = router;
Vue.prototype.$GaApi = GaApi;

Vue.use(ElementUI);
Vue.use(GaTool.gaComponent);

Vue.config.productionTip = false

new Vue({
  store,
  router,
  render: h => h(App),
}).$mount('#app')
