<template>
    <div class="blogInfo">
        <div class="title">
            {{blogInfo == null ? '':blogInfo.title}}
        </div>
        <div class="author">
            作者：{{blogInfo == null ? '':blogInfo.homemateInfoId}}
            主题：{{themeStr}}
            标签：{{tagStr}}
            发布日期：{{blogInfo == null ? '':blogInfo.pubTime}}
        </div>
        <div class="content" v-html="blogInfo == null ? '':blogInfo.content">
        </div>
        <div class="edit-btn">
            <el-button type="primary" icon="el-icon-edit" circle @click="toSavePage"></el-button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
      return {
          blogInfo: null,
          themeStr: null,
          tagStr: null,
      }
    },
    mounted(){
      this.getInfo();
    },
    methods: {
        toSavePage(){
            this.$router.push({name:'blogSave',query:{id: this.$route.query.id}});
        },
        getInfo(){
            let _this = this;
            this.$GaApi.getBlogInfoApi({id: this.$route.query.id}).then(res => {
                this.blogInfo = res.data
            }).then(()=>{
                this.$GaApi.getThemeListApi({page: -1}).then(res2=>{
                    let themeList = res2.data;
                    let _themeStr = [];
                    let themeArray = _this.blogInfo.themeInfo;
                    for(let index of themeList){
                        for(let index2 of themeArray){
                            if(index.id == index2.id){
                                _themeStr.push(index.name);
                            }
                        }
                    }
                    _this.themeStr = _themeStr.join(',');
                })
            }).then(() => {
                this.$GaApi.getTagListApi({page: -1}).then(res => {
                    let tagList = res.data;

                    let _tagStr = [];
                    let tagArray = _this.blogInfo.tagInfo;
                    for(let index of tagList){
                        for(let index2 of tagArray){
                            if(index.id == index2.id){
                                _tagStr.push(index.tagName);
                            }
                        }
                    }
                    _this.tagStr = _tagStr.join(',');
                })
            })
        },
    }
}
</script>

<style>
.blogInfo{
    display: flex;
    flex-direction: column;
}
.title{
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #333;
}
.author{
    margin-bottom: 1rem;
    color: #555;
}
.content{
    font-size: 1.2rem;
    color: #666;
    line-height: 1.8rem;
    font-family: "微软雅黑";
}
.edit-btn{
    position: fixed;
    right: 30px;
    bottom: 100px;
}
</style>
