<template>
    <div id="main">
        <el-container class="whoal-part">
            <home-nav :navlist="navlist" :isCollapse="isCollapse" class="left-part"/>
            <el-container class="right-part" direction="vertical">
                <ga-header :title="'标题'" :iconOnTap="headerIconOnTap" :logoutOnTap="headerlogoutOnTap"/>
                <el-main>
                    <section class="app-main">
                        <transition name="fade" mode="out-in">
                            <router-view></router-view>
                        </transition>
                    </section>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
export default {
    created(){
      this.getMenuData();
    },
    computed:{
    },
    methods:{
      headerIconOnTap(){
          this.$store.commit('increment')
          console.log(this.$store.state.count) // -> 1
          this.isCollapse = !this.isCollapse;
      },
      headerlogoutOnTap(){
          console.log('进行了退出操作')
          this.$store.commit('RESET_USER');
          this.$router.push({name:'login'});
      },
      getMenuData(){
          this.navlist = this.$gaPermission.getMenuJsonByData(this.$store.state.user.permissionArray);
      }
    },
    data() {
        return {
            navlist:[],
            isCollapse: false,
        }
    },
}
</script>
<style>
*{margin:0; padding:0;}
.whoal-part, #main, .left-part, .right-part, #app-main{
    height: 100%;
}
.el-header, .el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}
body > .el-container {
  margin-bottom: 40px;
}
.el-container:nth-child(5) .el-aside, .el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>