<template>
    <div id="old-version">
        <div id="top">
            选择草稿日期：
            <el-date-picker
                v-model="dateSelect"
                type="daterange"
                @change="onDateChange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                :picker-options="pickerOptions">
            </el-date-picker>
        </div>
        <div id="middle">
            <div>
                <el-table
                    size = 'mini'
                    border
                    :show-header="false"
                    height="99%"
                    ref="singleTable"
                    :data="oldVersionBlogList"
                    highlight-current-row
                    @current-change="handleCurrentChange"
                    style="width: 100%">
                    <el-table-column
                    label=""
                    width="120">
                        <template slot-scope="scope">
                            {{scope.row.updateTime.split(' ')[0]}}
                            <br>
                            {{scope.row.updateTime.split(' ')[1]}}
                            
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div id="middle-right">
                <div id="middle-right-top">
                    <el-button @click="sendMsgToFather">恢复到此版本</el-button>
                    {{currentRow==null||currentRow['title']== null?'':currentRow['title']}}
                </div>
                <!-- <el-input
                    id="middle-right-bottom"
                    type="textarea"
                    placeholder="请输入内容"
                    :rows="6"
                    v-model="textarea">
                </el-input> -->
                <div id="middle-right-bottom" v-html="currentRow==null||currentRow['content']==null?'':currentRow['content']">
                    
                </div>
            </div>
        </div>
        <div id="bottom">
              <el-pagination
                layout="prev, pager, next"
                :page-size="pageSize"
                :current-page.sync="currentPage"
                @current-change="onPageChange"
                :total="totalElements">
                </el-pagination>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        blogId: String
    },
    mounted() {
        this.getOldVersionBlogList();
    },
    methods: {
        onDateChange(val){
            
            console.log('日期改变'+val)
            this.getOldVersionBlogList()
        },
        getTime(param){
            if(!param){
                return '';
            }else{
                return param.replace(' ', '\n\r')
            }
        },
      setCurrent(row) {
        this.$refs.singleTable.setCurrentRow(row);
      },
      handleCurrentChange(val) {
        this.currentRow = val;
      },
      onPageChange(){
          this.getOldVersionBlogList()
      },
      getOldVersionBlogList(){
          let data = {
              blogId: this.blogId,
              page: this.currentPage - 1,
                size: this.pageSize,
          }

        if(this.dateSelect){
            data.startDate = this.dateSelect[0]
            data.endDate = this.dateSelect[1]
          }

          this.$GaApi.oldVersionBlogListApi(data).then(res=>{
              this.oldVersionBlogList = res.data;
              this.totalElements = parseInt(res.totalElements);
          })
      },
      sendMsgToFather(){
          if(this.currentRow){
            this.$emit('select',this.currentRow);
          }
      }
    },
    data(){
        return {
            dateSelect: null,
            currentPage: 1,
            totalElements: 0,
            pageSize: 10,

            oldVersionBlogList: [],
            currentRow: null,
            pickerOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                    picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                    picker.$emit('pick', [start, end]);
                    }
                }]
            },
        }
    }
}
</script>
<style scoped>
    #top{
        padding-left: 10px;
        margin-bottom: 10px;
    }
    #old-version{
        display: flex;
        flex-direction: column;
    }

    #middle{
        flex-grow: 1;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        height: 70%;
    }
    #middle-right{
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        margin-left: 5px;
    }
    #middle-right-top{
        margin-bottom: 3px;
    }
    #middle-right-bottom{
        /* flex-grow: 1; */
        overflow: auto;
        height: 75%;
    }
    .el-table__body-wrapper{
        height: 100%!important;
    }
</style>